import Swiper, { Autoplay } from 'swiper';

import 'swiper/swiper.scss';

export default function testimonialCarousel() {
  new Swiper('.testimonial-carousel', {
    modules: [Autoplay],
    slidesPerView: 3,
    centeredSlides: true,
    loop: true,
    autoplay: {
      delay: 2000,
    },
    spaceBetween: 65,
    speed: 700,
    breakpoints: {
      1440: {
        spaceBetween: 90,
      },
    },
  });
}
