export default function tooltip() {
  const tooltips = [].slice.call(document.querySelectorAll('[data-tooltip]'));
  const tooltip = `<span
    data-tooltip-over
      class="flex absolute left-1/2 whitespace-nowrap -translate-x-1/2 bg-black text-white px-2 py-1 rounded-md -top-8 text-sm items-center justify-center"
    >
      Coming soon
    </span>`;

  function showtooltip(e) {
    if (!this.querySelector('[data-tooltip-over]')) {
      this.insertAdjacentHTML('beforeend', tooltip);
      this.classList.add('relative');
    }
  }

  function hidetooltip(e) {
    this.querySelector('[data-tooltip-over]').remove();
    this.classList.remove('relative');
  }

  tooltips.forEach((element) => {
    element.addEventListener('mouseover', showtooltip);
    element.addEventListener('mouseout', hidetooltip);
  });
}
