import { isLargeDevices } from '../config';

function handleMousemove(e, popup) {
  const name = e.target.dataset.name;
  const post = e.target.dataset.post;

  popup.style.left = e.clientX + 10 + 'px';
  popup.style.top = e.clientY + 10 + 'px';

  if (popup.classList.contains('hidden')) {
    popup.classList.remove('hidden');
  }

  if (name) {
    popup.innerHTML = `<span class="block text-lg text-gray-800">${name}</span>
    <span class="block text-sm text-gray-600">${post}</span>`;
  }
}

function handleMousemout(_e, popup) {
  popup.classList.add('hidden');
}

export default function team() {
  const team = document.querySelector('.team');
  const popup = document.querySelector('.popup');

  if (isLargeDevices) {
    team.addEventListener('mousemove', (e) => handleMousemove(e, popup));
    team.addEventListener('mouseout', (e) => handleMousemout(e, popup));
  }
}
