import Swiper, { Autoplay, Navigation, Pagination } from 'swiper';

import 'swiper/swiper.scss';
import 'swiper/modules/effect-fade/effect-fade.scss';

export default function featureCarousel() {
  new Swiper('.feature-carousel', {
    modules: [Navigation, Pagination, Autoplay],
    slidesPerView: 1.5,
    spaceBetween: 20,
    autoplay: {
      delay: 2000,
    },
    speed: 700,
    pagination: {
      el: '.feature-carousel-dots',
      clickable: true,
      renderBullet: function (index, className) {
        return (
          '<span class="cursor-pointer flex items-center justify-center py-1 px-3 text-lg font-semibold text-gray-700 ' +
          className +
          '">' +
          (index + 1) +
          '</span>'
        );
      },
    },
    centeredSlides: true,
    breakpoints: {
      1200: {
        slidesPerView: 2.5,
        centeredSlides: false,
      },
    },
    navigation: {
      nextEl: '.feature-carousel-next',
      prevEl: '.feature-carousel-prev',
    },
  });
}
