import Swiper, { Pagination, Autoplay } from 'swiper';
import { isLargeDevices } from '../config';

import img9090 from '../images/img-9090.png';
import img9091 from '../images/img-9091.png';
import img9092 from '../images/img-9092.png';
import img9093 from '../images/img-9093.png';
import img9094 from '../images/img-9094.png';
import img9095 from '../images/img-9095.png';

const icons = {
  'img-9090': img9090,
  'img-9091': img9091,
  'img-9092': img9092,
  'img-9093': img9093,
  'img-9094': img9094,
  'img-9095': img9095,
};

import 'swiper/swiper.scss';
import './screenCarousel.scss';

export default function featureCarousel() {
  const slideLine = document.querySelector('.screen-line');

  if (isLargeDevices) {
    const slider = new Swiper('.screen-carousel', {
      modules: [Autoplay, Pagination],
      autoplay: {
        delay: 3000,
      },
      speed: 600,
      pagination: {
        el: '.screen-carousel-pagination',
        clickable: true,
        renderBullet,
      },
    });

    slider.on('slideChange', function (swiper) {
      const slideByIndex = swiper.slides[swiper.activeIndex];
      const slideData = slideByIndex.dataset;
      slideLine.innerHTML = slideData.paginationLine;
    });

    function renderBullet(index, className) {
      const slideByIndex = this.slides[index];
      const slideData = slideByIndex.dataset;

      return `<div
        class="px-5 py-4 flex items-center rounded-lg cursor-pointer mb-2 text-gray-400 ${className}"
      >
        <img
          class="block w-9 h-9 object-contain mr-5"
          src="${icons[slideData.paginationIcon]}"
          alt=""
        />
        <span class="uppercase text-md xl:text-lg"> ${
          slideData.paginationTitle
        } </span>
      </div>`;
    }
  } else {
    const container = document.querySelector('.screen-carousel');
    const items = [];

    slideLine.remove();

    [].slice
      .call(container.querySelectorAll('.swiper-slide'))
      .forEach((element) => {
        const data = element.dataset;
        const markup = `
          <div class="mb-10 text-center">
            <img
              class="block h-16 w-auto object-contain mx-auto my-7"
              src="${icons[data.paginationIcon]}"
              alt=""
            />
            <p class="text-2xl font-bold uppercase mb-2">${
              data.paginationTitle
            }</p>
            <p class="text-base max-w-lg mx-auto">${data.paginationLine}</p>
          </div>
        `;

        items.push(markup);
      });

    container.innerHTML = items.join('');
  }
}
